<template>
<div id="app">

  <app-header></app-header>
  <div class="contents">
    <div class="row">
      <div class="col-12">
        <h1>Geographical Area</h1>
        <p>
          Add and edit geographical areas from here.
        </p>
      </div>
    </div>

    <div class="row mt-4">
      <div class="col-sm-12 col-md-12 col-lg-6">
        <b-form class="card shadow">
          <h3 class="pb-3">Add Location</h3>
            <b-form-input type="text" class="mb-3" placeholder="Nepal" disabled></b-form-input>
            <b-form-select v-model="province_selected" class="mb-3">
              <template slot="first">
                <option value="" disabled>Select Your Province</option>
              </template>
              <option v-for="(province, district) in provinces" :value="province.name">{{ province.name }}</option>
            </b-form-select>

            <!-- <b-form-input type="text" class="mb-3" placeholder="Enter Your District"></b-form-input> -->
            <b-form-select v-model="district_selected" class="mb-3">
              <template slot="first">
                <option value="" disabled>Select Your District</option>
              </template>
              <option v-for="district in districts" :value="district">{{ district }}</option>
            </b-form-select>
            <b-form-input type="text" class="mb-3" v-model="street_address" placeholder="Enter Your Street Address"></b-form-input>
            <b-button variant="custom" @click="createGeography">Submit</b-button>
        </b-form>
      </div>

      <div class="col-sm-12 col-md-12 col-lg-6">
        <div class="card shadow data-list">
          <h3 class="pb-3">Location List</h3>
          <b-form-group>
            <b-input-group>
              <b-input-group-prepend>
                <b-button disabled variant="success" class="search-button"><i class="fas fa-search"></i></b-button>
              </b-input-group-prepend>
              <b-form-input v-model="filter" placeholder="Search for Address"></b-form-input>
              <b-input-group-append>
                <b-button variant="danger" :disabled="!filter" @click="filter = ''" class="search-button">Clear</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
            <!-- <b-form-input type="text" class="mb-3" placeholder="First Name"></b-form-input>
            <b-form-input type="text" class="mb-3" placeholder="Middle Name"></b-form-input>
            <b-form-input type="text" class="mb-3" placeholder="Last Name"></b-form-input>
            <b-form-input type="email" class="mb-3" placeholder="Email Address"></b-form-input>
            <b-button variant="primary" >Submit</b-button> -->
          <b-table
          id="user-table"
          show-empty
          :items="geography"
          :fields="fields"
          bordered
          :filter="filter"
          @filtered="onFiltered"
          responsive
          hover
          :current-page="currentPage"
          >
            <template slot="S.N." slot-scope="data">
              {{ data.index + 1 + '.' }}
            </template>
            <template slot="state" slot-scope="row">
              {{ row.item.state }}
            </template>
            <template slot="city" slot-scope="row">
              {{ row.item.city }}
            </template>
            <template slot="street_address" slot-scope="row">
              {{ row.item.street_address }}
            </template>
            <template slot="actions" slot-scope="row">
              <!-- <b-button variant="danger" @click="handleDeleteButton(program.id)">Delete</b-button> -->
              <b-button variant="outline-warning" v-b-modal="'location-modal'" @click="getgeographydetail(row.item.id)">Edit</b-button>
            </template>
          </b-table>
        </div>
      </div>
    </div>

    <b-toast id="error-toast" variant="warning" solid append-toast toaster="b-toaster-top-center">
      <div slot="toast-title" class="d-flex flex-grow-1 align-items-baseline">
        <strong class="mr-auto">Error!!</strong>
      </div>
      <div v-if="message">
        <p v-if="message.length>0">{{message}}</p>
      </div>
      <div v-if="errors.province_selected">
        <p>{{ errors.province_selected }}</p>
      </div>
      <div v-if="errors.district_selected">
        <p>{{ errors.district_selected }}</p>
      </div>
      <div v-if="errors.street_address">
        <p>{{ errors.street_address }}</p>
      </div>
    </b-toast>

    <b-toast id="success-toast" variant="custom-success" solid append-toast toaster="b-toaster-bottom-full">
      <div slot="toast-title" class="d-flex flex-grow-1 align-items-baseline">
        <strong class="mr-auto">Location Added</strong>
      </div>
        A new location is added.
    </b-toast>

    <b-modal ref="modal1" id="location-modal" centered title="Edit a Location" hide-footer>
      <b-form @submit.prevent>
        <b-form-input type="text" class="mb-3" placeholder="Nepal" disabled></b-form-input>
        <b-form-select v-model="province_selected_edit" class="mb-3">
              <template slot="first">
                <option value="" disabled>Select Your Province</option>
              </template>
              <option v-for="(province, district) in provinces" :value="province.name">{{ province.name }}</option>
            </b-form-select>

            <!-- <b-form-input type="text" class="mb-3" placeholder="Enter Your District"></b-form-input> -->
            <b-form-select v-model="district_selected_edit" class="mb-3">
              <template slot="first">
                <option value="" disabled>Select Your District</option>
              </template>
              <option v-for="district in districts_edit" :value="district">{{ district }}</option>
            </b-form-select>
        <b-form-input type="text" class="mb-3" v-model="geography_obj.street_address" placeholder="Enter Your Street Address"></b-form-input>
        <div class="d-flex justify-content-center align-items-center" style="flex-direction: column;">
          <b-button variant="custom" @click="updateGeographyForm">Update</b-button>
          <a href="javascript:void(0);" @click="deleteGeography" class="text-danger mt-3"><i class="fas fa-trash pr-2"></i>Delete the location</a>
        </div>
      </b-form>
    </b-modal>
  </div>
</div>
</template>

<script>
import { mapState,mapActions } from 'vuex';
import AppHeader from './Header.vue'
import province from "../js/districts.js"

// const axios = require('axios');
export default {
  name: "Geography",
  components:{
    // "AuthenticationForm": AuthenticationForm
    'app-header': AppHeader
  },
  computed: {
    ...mapState(['geography','token','message','successmessage','errormessage']),
    districts: function(){
      if(this.province_selected != ""){
        return this.provinces.find(province => province.name == this.province_selected ).districts
      }
    },

    districts_edit: function(){
      if(this.province_selected_edit != ""){
        return this.provinces.find(province => province.name == this.province_selected_edit ).districts
      }
    }
  },

  created(){
    this.listGeography();
  },

  data() {
    return {
      province_selected: '',
      district_selected: '',
      province_selected_edit: '',
      district_selected_edit: '',
      provinces: province,
      filter: null,
      currentPage: 1,
      street_address:'',
      errors:[],
      geography_obj:'',
      fields: [
        'S.N.',
        { key: 'address', label: 'Address'},
        { key: 'state', label: 'Province', sortable: true},
        { key: 'actions', label: 'Action' },
      ],
      // locations:[
      //   {address: 'Babarmahal', district: 'Kathmandu'},
      //   {address: 'Mangalbazar', district: 'Lalitpur'},
      //   {address: 'Kamalbinayak', district: 'Bhaktapur'},
      //   {address: 'Dhulikhel', district: 'Kavrepalanchok'},
      // ]
      // username: '',
      // password: '',
      // text: "Login Form",
      // available: false,
      // show: false,
      // errors: {'auth':''},
      // disabledLogin: true
    }
  },

  methods:{
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },
    ...mapActions(['listGeography']),
    createGeography(){
      this.errors=[]
      if(this.province_selected==''){
        this.errors['province_selected']="Province required."
        this.$bvToast.show('error-toast');
      }else if(this.district_selected == ''){
        this.errors['district_selected']="District required."
        this.$bvToast.show('error-toast');
      }else if(this.street_address == ''){
        this.errors['street_address'] = "Street Address required."
        this.$bvToast.show('error-toast');
      }else(
        this.$store.dispatch("createGeography", {
          'state': this.province_selected,
          'city':this.district_selected ,
          'street_address':this.street_address,
          'country':'Nepal',
        }).then(() => {
          if(this.successmessage=='success'){
            this.$bvToast.show('success-toast');
          }else if(this.errormessage=='errormessage'){
            this.$bvToast.show('error-toast');

          }
        })
        )
      this.province_selected = '';
      this.district_selected = '';
      this.street_address = '';
    },

    updateGeographyForm(){
      this.errors=[]
      if(this.province_selected_edit==''){
        this.errors['province_selected_edit']="Province required."
        this.$bvToast.show('error-toast');
      }else if(this.province_selected_edit == ''){
        this.errors['district_selected_edit']="District required."
        this.$bvToast.show('error-toast');
      }else if(this.geography_obj.street_address == ''){
        this.errors['street_address'] = "Street Address required."
        this.$bvToast.show('error-toast');
      }
      else{
        this.$store.dispatch("updateGeography", {
          'state': this.province_selected_edit,
          'city':this.district_selected_edit ,
          'street_address':this.geography_obj.street_address,
          'country':'Nepal',
          'id': this.geography_obj.id,
        }).then(() => {
          if(this.successmessage=='success'){
            location.reload()
          }else if(this.errormessage=='errormessage'){
            this.$bvToast.show('error-toast');

          }
        })
      }
  },

  deleteGeography(){
    this.$store.dispatch("deleteGeography",this.geography_obj.id)
    .then(() => {
      if(this.successmessage=='success'){
        location.reload()
      }
    })
  },

  getgeographydetail(geography_id){
    this.geography_obj = this.geography.find(evt => evt.id == geography_id)
  },


  }
};
</script>

<style lang="scss" scoped>
  @import "../css/index.scss";

</style>
