<template>
<div id="app">

  <app-header></app-header>
  <div class="contents">
    <div class="row">
      <div class="col-12">
        <h1>Users</h1>
        <p>
          Add and edit users from here.
        </p>
      </div>
    </div>

    <div class="row mt-4">
      <div class="col-sm-12 col-md-12 col-lg-6">
        <b-form class="card shadow">
          <h3 class="pb-3">Add Users</h3>
            <b-form-input type="text" class="mb-3" v-model="first_name" placeholder="First Name"></b-form-input>
            <b-form-input type="text" class="mb-3" v-model="middle_name" placeholder="Middle Name"></b-form-input>
            <b-form-input type="text" class="mb-3" v-model="last_name" placeholder="Last Name"></b-form-input>
            <b-form-input type="email" class="mb-3" v-model="email" placeholder="Email Address"></b-form-input>
            <multiselect
            class="mb-3"
            v-model="location"
            :options="options"
            :multiple="true"
            :close-on-select="false"
            :clear-on-select="false"
            :preserve-search="true"
            placeholder="Select Location"
            label="name"
            track-by="name"
            :preselect-first="true"
            >
            </multiselect>


            <b-button variant="custom" @click="createUser">Submit</b-button>
        </b-form>
      </div>

      <div class="col-sm-12 col-md-12 col-lg-6">
        <div class="card shadow data-list">
          <h3 class="pb-3">Users List</h3>
          <b-form-group>
            <b-input-group>
              <b-input-group-prepend>
                <b-button disabled variant="success" class="search-button"><i class="fas fa-search"></i></b-button>
              </b-input-group-prepend>
              <b-form-input v-model="filter" placeholder="Search for Users"></b-form-input>
              <b-input-group-append>
                <b-button variant="danger" :disabled="!filter" @click="filter = ''" class="search-button">Clear</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
          <b-table
          id="user-table"
          show-empty
          :items="users"
          :fields="fields"
          bordered
          :filter="filter"
          @filtered="onFiltered"
          responsive
          hover
          :current-page="currentPage"
          >
            <template slot="S.N." slot-scope="data">
              {{ data.index + 1 + '.' }}
            </template>

            <template slot="full_name" slot-scope="row">
              {{ row.item.full_name }}
            </template>
            <template slot="email" slot-scope="row">
              {{ row.item.email }}
            </template>
            <template slot="actions" slot-scope="row">
              <!-- <b-button variant="danger" @click="handleDeleteButton(program.id)">Delete</b-button> -->
              <b-button variant="outline-warning" centered v-b-modal="'user-modal'" @click="getuserdetail(row.item.id)">Edit</b-button>
            </template>
          </b-table>
        </div>
      </div>
    </div>

  <b-toast id="error-toast" variant="warning" solid append-toast toaster="b-toaster-bottom-full">
    <div slot="toast-title" class="d-flex flex-grow-1 align-items-baseline">
      <strong class="mr-auto">User Add Error</strong>
    </div>
    <div v-if="message">
      <p v-if="message.length>0">{{message}}</p>
    </div>
    <div v-if="errors.first_name">
      <p>{{ errors.first_name }}</p>
    </div>
    <div v-if="errors.last_name">
      <p>{{ errors.last_name }}</p>
    </div>
    <div v-if="errors.email">
      <p>{{ errors.email }}</p>
    </div>
    <div v-if="errors.province_selected">
      <p>{{ errors.province_selected }}</p>
    </div>
    <div v-if="errors.location">
      <p>{{ errors.location }}</p>
    </div>
  </b-toast>

  <b-toast id="success-toast" variant="custom-success" solid append-toast toaster="b-toaster-bottom-full">
    <div slot="toast-title" class="d-flex flex-grow-1 align-items-baseline">
      <strong class="mr-auto">User Added</strong>
    </div>
      A new user is added.
  </b-toast>


  <b-modal ref="modal1" id="user-modal" centered title="Edit a User" hide-footer>
    <b-form @submit.prevent>
      <b-form-input type="text" class="mb-3" v-model="user_obj.first_name" placeholder="First Name"></b-form-input>
      <b-form-input type="text" class="mb-3" v-model="user_obj.middle_name" placeholder="Middle Name"></b-form-input>
      <b-form-input type="text" class="mb-3" v-model="user_obj.last_name" placeholder="Last Name"></b-form-input>
      <b-form-input type="email" class="mb-3" v-model="user_obj.email" placeholder="Email Address"></b-form-input>

      <multiselect
      class="mb-3"
      v-model="location_edit"
      :options="options"
      :multiple="true"
      :close-on-select="false"
      :clear-on-select="false"
      :preserve-search="true"
      placeholder="Select Location"
      label="name"
      track-by="name"
      :preselect-first="true"
      >
      </multiselect>


      <!-- <b-form-select v-model="province_selected_edit" class="mb-3">
        <template slot="first">
          <option value="" disabled>Select Your Province</option>
        </template>
        <option v-for="(province, district) in provinces" :value="province.name">{{ province.name }}</option>
      </b-form-select>

      <b-form-select v-model="location_edit" class="mb-3" multiple>
        <template slot="first">
          <option value="" disabled>Select Your Location</option>
          <option :value="user_obj.location.id" v-for="user_address in user_obj.location">{{ user_address }}</option>
        </template>
        <option :value="address.id" v-for="(address,index) in geography" v-if="province_selected_edit==address.state" :key="index">{{ address.address }}</option>
      </b-form-select> -->
      <div class="d-flex justify-content-center align-items-center" style="flex-direction: column;">
        <b-button variant="custom" @click="updateUserForm">Update</b-button>
        <a href="javascript:void(0);" @click="deleteUser" class="text-danger mt-3"><i class="fas fa-trash pr-2"></i>Delete the user</a>
      </div>
    </b-form>
  </b-modal>
  </div>
</div>
</template>

<script>
import { mapState,mapActions } from 'vuex';
import AppHeader from './Header.vue';
import province from "../js/districts.js"

export default {
  name: "Users",
  components:{
    'app-header': AppHeader
  },
  computed: {
    ...mapState(['users','message','successmessage','errormessage','geography'])
  },

  created() {
    this.listUsers();
    this.listGeography().then(() => {
      this.updateOptions();})

  },
  mounted: function() {
    if (this.$store.state.token == "") {
      this.$router.replace("/");
    }

  },

  data() {
    return {
      value: '',
      options: [
        // { name: 'Vue.js', language: 'JavaScript' },
        // { name: 'Adonis', language: 'JavaScript' },
        // { name: 'Rails', language: 'Ruby' },
        // { name: 'Sinatra', language: 'Ruby' },
        // { name: 'Laravel', language: 'PHP' },
        // { name: 'Phoenix', language: 'Elixir' }
      ],
      provinces: province,
      province_selected: '',
      province_selected_edit: '',
      filter: null,
      currentPage: 1,
      first_name: '',
      last_name: '',
      middle_name: '',
      location: [],
      location_edit: [],
      user_obj: {},
      email: '',
      user_location:[],
      user_location_edit:[],
      errors:[],
      fields: [
        'S.N.',
        { key: 'full_name', label: 'Full Name', sortable: true},
        { key: 'email', label: 'Email'},
        { key: 'actions', label: 'Action' },
      ],
      // users:[
      //   {full_name: 'Manish Raj Silwal', email: 'manishrajsilwal@gmail.com'},
      //   {full_name: 'Kshitiz Gartaula', email: 'user@gmail.com'},
      //   {full_name: 'Prabin Karki', email: 'pkkari18@gmail.com'},
      //   {full_name: 'Rabindra Lamsal', email: 'rabindra@gmail.com'},
      //   {full_name: 'Kusum Humagain', email: 'khumagain@gmail.com'},
      //   {full_name: 'Kapindra Raut', email: 'kapin@gmail.com'},
      //   {full_name: 'Hema Rimal', email: 'hrimalhema@gmail.com'},
      //   {full_name: 'Chandra Dangi', email: 'cdangi@gmail.com'},
      //   {full_name: 'Babita Jha', email: 'bjhabab@gmail.com'},
      // ]
    }
  },

  methods:{
    ...mapActions(["listUsers","listGeography"]),
    createUser(){
      this.errors=[]
      var geography_id =[]
      this.location.forEach(function(location_id){
          geography_id.push(location_id.language)
        })
      this.user_location = geography_id
      if(this.first_name==''){
        this.errors['first_name']="First Name required."
        this.$bvToast.show('error-toast');
      }else if(this.last_name == ''){
        this.errors['last_name']="Last Name required."
        this.$bvToast.show('error-toast');
      }else if(this.email == ''){
        this.errors['email'] = "Email required."
        this.$bvToast.show('error-toast');
      }else if(this.location == ''){
        this.errors['location'] = "Location required."
        this.$bvToast.show('error-toast');
      }else(
        this.$store.dispatch("createUser", {
          'first_name': this.first_name,
          'last_name':this.last_name ,
          'middle_name':this.middle_name,
          'email':this.email,
          'area':this.user_location
        }).then(() => {
          if(this.successmessage=='success'){
            // this.users.push(this.user_obj)
            this.$bvToast.show('success-toast');
          }else if(this.errormessage=='errormessage'){
            this.$bvToast.show('error-toast');

          }
        })
      )
        this.first_name = '';
        this.last_name = '';
        this.middle_name = '';
        this.email = '';
        this.location = '';
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },

    updateUserForm(){
      var geography_edit_id =[]
      this.location_edit.forEach(function(location_id){
          geography_edit_id.push(location_id.language)
        })
      this.user_location_edit = geography_edit_id
      console.log(this.user_location_edit)
       this.$store.dispatch("updateUser", {
        'first_name': this.user_obj.first_name,
        'last_name':this.user_obj.last_name ,
        'middle_name':this.user_obj.middle_name,
        'email':this.user_obj.email,
        'area':this.user_location_edit,
        'id': this.user_obj.id,
      }).then(() => {
        if(this.successmessage=='success'){
          location.reload()
          }else if(this.errormessage=='errormessage'){
            this.$bvToast.show('error-toast');

          }
        })
    },
    deleteUser(){
      this.$store.dispatch("deleteUser",this.user_obj.id)
      .then(() => {
        if(this.successmessage=='success'){
          location.reload()
          }
        })

    },
    getuserdetail(user_id){
      this.user_obj = this.users.find(evt => evt.id == user_id)
      var selectedGeography = []
      this.user_obj.location.forEach(function(elem){
        selectedGeography.push({'name': elem})
      })
      this.location_edit = selectedGeography
      console.log(this.location_edit)
    },
    updateOptions(){
      var geography_data=[]
      if (this.geography.length>0){
        this.geography.forEach(function(geography_obj){
          geography_data.push({'name':geography_obj.location,'language':geography_obj.id})
        })
        this.options = geography_data
      }

    }

  }
};
</script>

<style lang="scss" scoped>
  @import "../css/users.scss";

</style>
